/* ------- Theme.scss ----------- */
.topmenu {
  .title_block {
    text-align: right;
    i {
      float: right;
    }
  }
}
.compare {
  > a{
    &.bt_compare{
     padding: 0 25px 0 0px;
     &:after {
      right: 0;
      left: auto;
    }
  }
}
}
.wishtlist_top {
  > a{
    &.wishlist-logo {
      padding: 0 25px 0 0px;
      &:after {
        right: 0;
        left: auto;
      }
    }
  }
}
#header {
  #header_logo {
    float: right;
  }
  .position-static {
    .topmenu {
     float: right;
   }
 }
 .header-nav {
  .left-nav {
    text-align: right;
    @media (max-width: 767px) {
      text-align: center;
    }
    > div {
      &:before {
        margin-left: 0px;
        margin-right: -12px;
      }
    }
  }
  .right-nav {
    text-align: left;
    > div {
      text-align: right;
    }
    @media (max-width: 767px) {
      text-align: center;
    }
  }
  .language-selector, .currency-selector {
    margin-right: 16px;
    margin-left: 0;
  }
  #contact-link {
    i {
      margin-right: 0px;
      margin-left: 16px;
    }
  }
}
#language_selector {
  width: auto;
}
#language_selector, #currency_selector {
  .expand-more {
    strong {
      margin-right: 0px;
      margin-left: 3px;
    }
    img {
      margin-right: 0px;
      margin-left: 2px;
    }
  }
  .dropdown-menu {
    left: 0;
    right: auto;
    text-align: right;
    @media (max-width: 992px) { 
      float: right;
    }
    li {
      .dropdown-item {
        margin-left: 0px;
        margin-right: 2px;
      }
    }
  }
}
.header-top {
}
.nav-full-width {
 .title_block {
  &:after {
    left: auto;
    right: 15px;
  }
}
#rvheaderlink_block {
  float: right;
  @media (min-width: 992px) { 
    padding: 0 25% 0 0;
  }
  .rvheaderlink_content {
    .bullet {
      text-align: right;
      > li {
        &.more {
         ul {
          li {
            text-align: right;
          }
        }
      }
    }
  }
}
}
}
.user-info {
  float: left;
  @media (max-width: 991px) { 
    float: none;
  }
  .user-info-inner {
    .dropdown-menu {
      left: 0;
      right: auto;
      text-align: right;
      @media (max-width: 991px) { 
        float: right;
      }
      &:after {
        left: 0;
        right: auto;
      }
    }
  }
  .login {
    padding: 0 25px 0 0;
    &:after {
      left: auto;
      right: -4px;
    }
  }
  .account {
    padding: 0 25px 0 0;
    &:after {
      left: auto;
      right: 0px;
    }
  }
  .logout {
    padding: 0 25px 0 0;
    &:after {
      left: auto;
      right: 0px;
    }
  }
}
.blockcart {
  float: left;
  .shopping_cart {
    > a {
      .cart-title {
        display: none;
      }
      .cart-products-count {
        right: auto;
        left: 3px;
        .cart_quantity_count {
          right: auto;
          left: 18px;
        }
      }
      .cart-products-item {
        left: 0;
        right: 25px;
      }
    }

    .cart_block {
      left: 15px;
      right: auto;
      @media (max-width: 991px) {
        left: 0;
      }
      .block_content {
        &:after {
          left: 0;
          right: auto;
        }
      }
      .cart_block_list {
        ul.products {
          li {
            text-align: right;
            .cart-images {
              float: right;
              margin-right: 0;
              margin-left: rem(10px);
            }
            .cart-info {
              padding-right: 0;
              padding-left: rem(20px);
            }
            .remove_link {
              right: auto;
              left: rem(10px);
            }
          }
        }
        .cart-prices {
          .price {
            .label {
              float: right;
            }
            .value {
              float: left;
            }
          }
        }
        .cart_block_no_products {
          text-align: right;
        }
      }
    }
  }
}
}

.backtotop {
  right: auto;
  left: 20px;
}



#left-column, #right-column {
  .block {
    .title_block {
      text-align: right;
      &:before {
        right: 0px;
        left: auto;
      }
      @media (max-width: 991px) {
        &:after {
          right: auto;
          left: 0;
        }
      }
    }
  }
  .products_block {
