/* ------- checkout.scss No Change ----------- */
body#checkout {
  .custom-checkbox {
    span {
      margin-right: 0;
      margin-left: rem(8px);
    }
  }
  section.checkout-step {
    &.-reachable.-complete {
      h1 {
        .step-edit {
          float: left;
        }
      }
    }
    .payment-options {
      .custom-radio {
        margin-right: 0;
        margin-left: $medium-space;
      }
    }
    .address {
      margin-left: 0;
      margin-right: rem(25px);
    }
    .radio-block {
      text-align: right;
    }
    .custom-radio {
      margin-right: 0;
      margin-left: 5px;
    }
    .carrier-delay,
    .carrier-name {
      text-align: right;
    }
    #customer-form,
    #delivery-address,
    #invoice-address,
    #login-form {
      margin-left: 0;
      margin-right: rem(5px);
      .form-control-label {
        text-align: right;
      }
      .radio-inline {
        padding: 0 0 0 20px;
      }
    }
    .forgot-password {
      margin-left: 0;
      margin-right: rem(230px);
    }
  }
  .additional-information {
    margin-left: 0;
    margin-right: rem(46px);
  }
  .condition-label {
    margin-left: 0;
    margin-right: rem(40px);
  }
}
#order-details {
  padding-left: 0;
  padding-right: $large-space;
}
#order-items {
  border-right: medium none;
  border-left: $gray-light 1px solid;
  table {
    tr {
      td {
        &:last-child {
          text-align: left;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .condition-label {
    label[for="conditions_to_approve[terms-and-conditions]"] {
      text-align: right;
    }
  }
}
@media (max-width: 767px) {
  body#checkout section.checkout-step.-reachable.-complete h1 .step-edit {
    float: none;
  }
  #order-items {
    border-left: 0;
  }
  #order-details {
    padding-right: rem(15px);
  }
}
@media (max-width: 543px) {
  body#checkout {
    section.checkout-step {
      .content {
        padding: rem(15px) 0;
      }
    }
  }
}

